.slider {
  height: 340px;
  max-width: 680px;
}

.slider-slide {
  height: 300px !important;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.slider-text {
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  padding: 0 20px;
  height: 30px;
  line-height: 30px;
  background-color: #000;
  color: #fff;
  opacity: 0.8;
}

.swiper-pagination.swiper-pagination-clickable.swiper-pagination-bullets.swiper-pagination-horizontal {
  bottom: 0;
  text-align: right;
}

.swiper-button-next, .swiper-button-prev {
  color: #11bdd1 !important;
}

.swiper-pagination-bullet {
  width: 14px !important;
  height: 18px !important;
  background: url('http://127.0.0.1:5501/images/bullets.png') 0 0 !important;
  border-radius: 0 !important;
  opacity: 1 !important;
  &.swiper-pagination-bullet-active {
    background: url('http://127.0.0.1:5501/images/bullets.png') 0 100% !important;
  }
}